import { Component, OnInit } from '@angular/core';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { UtilizationTableDataRow } from '../../interfaces';

@Component({
  selector: 'ism-sdpp',
  templateUrl: './sdpp.component.html',
  styleUrls: ['./sdpp.component.styl']
})
export class SdppComponent implements OnInit {
  ServiceData: UtilizationTableDataRow[];
  PeopleData: UtilizationTableDataRow[];
  ProcessData: UtilizationTableDataRow[];

  constructor(private calcService:CalcService) { }

  ngOnInit() {
    this.initializeServiceData();
    this.initializePeopleData();
    this.initializeProcessData();
  }

  initializeServiceData(){
    this.ServiceData=[
      {
        columns: [
          {
            type: 'output'
          },
          {
            type: 'output',
            ref: 'tlOutput_E5_tlSDPP',
            color: '#00a1e0'
          },
          {
            type: 'output',
            ref: 'tlOutput_H5_tlSDPP',
            color: '#00a1e0'
          }
        ]
      },
      {
        columns: [
          {
            type: 'output',
            ref: 'tlOutput_C7_tlSDPP',
            color: '#73808b',
            align:'left'
          },
          {
            type: 'stepper',
            ref: 'tlInputSDPPSecurity',
            step:+this.calcService.getValue("tlOutputValStepSDPPSecurity"),
            min:+this.calcService.getValue("tlOutputValMinSDPPSecurity"),
            max:+this.calcService.getValue("tlOutputValMaxSDPPSecurity"),
            format:"0",
            color: '#00a1e0',
            fte:true
          },
          {
            type: 'slider',
            name:'tlOutput_H5_tlSDPP',
            ref: 'tlInputSDPPDatacenterTool',
            min:1,
            max:3,
            step:1,
            sliderValues:["Low","Medium","High"],
            color: '#00a1e0'
          }
        ]
      },
      {
        columns: [
          {
            type: 'output',
            ref: 'tlOutput_C11_tlSDPP',
            color: '#73808b',
            align:'left'
          },
          {
            type: ''
          },
          {
            type: 'output',
            ref: 'tlOutput_H11_tlSDPP',
            format: '$0,0 am',
            color: '#00a1e0'
          }
        ]
      },
      {
        columns: [
          {
            type: 'output',
            ref: 'tlOutput_C13_tlSDPP',
            color: '#73808b',
            align:'left'
          },
          {
            type: 'output',
            ref: 'tlOutput_E11_tlSDPP',
            color: '#00a1e0'
          },
          {
            type: 'output'
          }
        ]
      }
    ]
  }

  initializePeopleData(){
    this.PeopleData =[
      {
        columns: [
          {
            type: 'output',
            ref: 'tlOutput_C21_tlSDPP',
            color: '#73808b',
            align:'left'

          },
          {
            type: 'stepper',
            ref: 'tlInputSDPPTalent',
            step:1,
            max:5,
            min:0,
            format:"0",
            color: '#00a1e0'
          },
          {
            type: ''
          }
        ]
      },
      {
        columns: [
          {
            type: 'output',
            ref: 'tlOutput_C17_tlSDPP',
            color: '#73808b',
            align:'left'
          },
          {
            type: 'output',
            ref: 'tlOutput_E17_tlSDPP',
            format:'$0,0 am',
            color: '#00a1e0'
          },
          {
            type: ''
          }
        ]
      },
      {
        columns: [
          {
            type: 'output',
            ref: 'tlOutput_C19_tlSDPP',
            color: '#73808b',
            align:'left'
          },
          {
            type: 'output',
            ref: 'tlOutput_E19_tlSDPP',
            color: '#00a1e0'
          },
          {
            type: ''
          }
        ]
      },
      {
        columns: [
          {
            type: 'text',
            key: 'Equality',
            color: '#73808b',
            align:'left'
          },
          {
            type: 'stepper',
            ref: 'tlInputSDPPEquality',
            step:1,
            max:5,
            min:0,
            format:"0",
            color: '#00a1e0'
          },
          {
            type: ''
          }
        ]
      },
      {
        columns: [
          {
            type: 'output',
            ref: 'tlOutput_C17_tlSDPP',
            color: '#73808b',
            align:'left'
          },
          {
            type: 'output',
            ref: 'tlOutput_H17_tlSDPPEquality',
            format:'$0,0 am',
            color: '#00a1e0'
          },
          {
            type: ''
          }
        ]
      },
      {
        columns: [
          {
            type: 'output',
            ref: 'tlOutput_C19_tlSDPP',
            color: '#73808b',
            align:'left'
          },
          {
            type: 'output',
            ref: 'tlOutput_H19_tlSDPPEquality',
            color: '#00a1e0'
          },
          {
            type: ''
          }
        ]
      }
    ]
  }

  initializeProcessData(){
    this.ProcessData =[
      {
        columns:[
          {
            type: 'output',
            ref: 'tlOutput_C25_tlSDPP',
            color: '#73808b',
            align:'left'
          },
          {
            type: 'stepper',
            ref: 'tlInputSDPPGTMImprove',
            step: 1,
            min:0,
            max:5,
            format:"0",
            color: '#00a1e0'
          },
          {
            type: ''
          }
        ]

      },
      {
        columns:[
          {
            type: 'output',
            ref: 'tlOutput_C26_tlSDPP',
            color: '#73808b',
            align:'left'
          },
          {
            type: 'output',
            ref: 'tlOutput_E26_tlSDPP',
            format:'$0,0 am',
            color: '#00a1e0'
          },
          {
            type: ''
          }
        ]
      },
      {
        columns:[
          {
            type: 'output',
            ref: 'tlOutput_C28_tlSDPP',
            color: '#73808b',
            align:'left'
          },
          {
            type: 'output',
            ref: 'tlOutput_E28_tlSDPP',
            color: '#00a1e0'
          },
          {
            type: ''
          },
        ]
      }
    ]
  }

}
