import { Component, Input, OnInit, AfterViewInit, EventEmitter, ViewChild, OnDestroy, HostListener, Output, ChangeDetectorRef, ViewChildren, QueryList } from '@angular/core';
import { Subscription } from 'rxjs';
import { CalcService, StorageService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { SingleWobblerComponent } from '../singlewobbler/singlewobbler.component';
import { TextEngineService } from '../../shared/textengine/textengine.service';
import { Router } from '@angular/router';
import { CommonModalComponent } from '../../shared/common-modal/common-modal.component';
import { Constants as AppConstants } from '../../config/constants';
import { WobblerTimingService } from '../wobbler-timing.service';

const KEY_NUM_4 = 52;

@Component({
  selector: 'isma-wobblerlibrary',
  templateUrl: 'wobblerlibrary.component.html',
  styleUrls: ['./wobblerlibrary.component.styl']
})

export class WobblerLibraryComponent implements OnInit, OnDestroy {
  wobblerIndex = -1;
  wobblersArray = [];
  // wobblersOverrideList = [];
  private wobblerLoc = -1;
  private reinitializeEventListener: Subscription;
  private currentRound;
  overriddenWobblerIndex: number = 0;
  currentRoundTimeBasedWobblersCompleted: boolean = true;
  private wobblerTimingSubscription: Subscription;
  private triggerListener: Subscription;
  @ViewChild('wobblerOverrideModal', { static: true }) wobblerOverrideModel: CommonModalComponent;
  // @ViewChild('WobblerListModal', { static: true }) public wobblerListModalRef: ModalDirective;
  @ViewChildren('singleWobbler') WOBBLER_INSTANCE_QUERYLIST: QueryList<SingleWobblerComponent>
  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEY_NUM_4) {
      this.overrideWobblers();
    }
  }

  constructor(
    private calcService: CalcService,
    private textService: TextEngineService,
    private storageService: StorageService,
    private communicatorservice: CommunicatorService,
    private cdRef: ChangeDetectorRef,
    private wobblerTiming: WobblerTimingService,
    private router: Router
  ) { }

  ngOnInit() {
    this.currentRound = this.calcService.getValue('tlInputTeamRound');
    const self = this;
    this.initializeWobblers();

    this.reinitializeEventListener = this.communicatorservice.getEmitter(AppConstants.WOBBLER_CONSTANTS.REINITIALIZE_WOBBLERS).subscribe(() => {
      self.initializeWobblers();
      self.storageService.setValue(AppConstants.WOBBLER_CONSTANTS.WOBBLERS_INITIALIZED, '1');
    });

    this.wobblerTimingSubscription = this.wobblerTiming.getObservable().subscribe(() => {
      this.showWobbler(this.wobblersArray[this.wobblerLoc]);
    });

    this.triggerListener = this.communicatorservice.getEmitter(AppConstants.EVENTS.MANUALLY_OPEN_WOBBLER).subscribe(()=>{
      this.overrideWobblers();
    })

    // this.communicatorservice.getEmitter('WobblerListPopup').subscribe(data => {
    //   this.wobblerListModalRef.show();
    // });
  }

  ngOnDestroy() {
    this.reinitializeEventListener.unsubscribe();
    this.wobblerTimingSubscription.unsubscribe();
    this.triggerListener.unsubscribe();
  }

  initializeWobblers() {
    const wobblers = this.textService.getInitiatives("Wobbler");
    let index = 0;
    this.wobblerLoc = -1;
    this.wobblersArray = [];
    this.currentRound = this.calcService.getValue('tlInputTeamRound');

    for (const key in wobblers) {
      if (wobblers.hasOwnProperty(key)) {
        const localScene = [];
        for (const property in wobblers[key]) {
          if (wobblers[key].hasOwnProperty(property)) {
            localScene[property] = wobblers[key][property];
          }
        }

        localScene['Round'] = parseInt(localScene['ID'].substr(1, 1));
        // localScene['WobblerCode'] = parseInt(localScene['wobblerRange'].substr(15)); // remove tlInputWobblerW from Wobbler Range
        localScene['isViewed'] = (parseInt(this.calcService.getValue(localScene['wobblerRange']), 10) === 0) ||
          (this.calcService.getValue(localScene['wobblerRange']) === undefined) ? false : true;

        if (localScene['Round'] == +this.currentRound) {
          this.wobblersArray.push(localScene);
          const completed = (this.calcService.getValue(localScene['wobblerRange']) !== '0');
          localScene['isViewed'] = completed;
          if (!completed && (this.wobblerLoc === -1)) {
            this.wobblerLoc = index;
          }
          index++;
        }
      }
    }
    this.cdRef.detectChanges();

    //Start Timer
    if (this.wobblerLoc != -1) {
      this.currentRoundTimeBasedWobblersCompleted = false;
      this.startWobllerTiming();
    }
  }



  //Wobbler Override
  overrideWobblers() {
    if (this.wobblersArray.length !== 0) {
      this.wobblerOverrideModel.openModal();
    } else { return; }
  }

  checkIfEnabled(wobbler) {
    let isActive = true;
    if (wobbler['wobblerRange']) {
      isActive = this.calcService.getValue(wobbler['wobblerRange']) == '0' ? true : false;
    }
    return isActive;
  }

  activateOverridenWobbler() {
    this.wobblerOverrideModel.closeModal();
    setTimeout(()=>{
      this.showWobbler(this.wobblersArray[this.overriddenWobblerIndex]);
    },1000)
  }

  showWobbler(wobbler) {
    if (wobbler) {
      this.showSingleWobblerAndAddBackdrop(wobbler);
    }
  }

  showSingleWobblerAndAddBackdrop(wobblerRef) {
    if(this.checkIfEnabled(wobblerRef)){
      // Trigger
      try{
        this.calcService.setValue(wobblerRef['wobblerTrigger'], 1);
      }catch(e){
        //In case if the wobbler is time based and does not have trigger the above statement will give an error. 
        //Trigger Manually.
        this.WOBBLER_INSTANCE_QUERYLIST.toArray()[this.overriddenWobblerIndex].showWobbler();
      }
    }else{
      //Read Only
      this.WOBBLER_INSTANCE_QUERYLIST.toArray()[this.overriddenWobblerIndex].showWobbler();
    }
  }


  //Timed Wobbler
  startWobllerTiming() {
    const wobblerInstance = this.wobblersArray[this.wobblerLoc];
    // let self = this;
    this.storageService.getValue(AppConstants.WOBBLER_CONSTANTS.WOBBLER_TIMING).then((res) => {
      let storedTime = res && res != '' ? parseInt(res.toString()) : 0;
      let Actualtime;
      if(wobblerInstance["time"]){
        if(wobblerInstance["time"].indexOf('tlInput') > 0 || wobblerInstance["time"].indexOf('tlOutput') > 0){
          Actualtime = +this.calcService.getValue(wobblerInstance["time"])
        }else{
          Actualtime = +wobblerInstance["time"];
        }
      }else{
        this.triggerNextWobbler();
        return;
      }
      let remainingTime = Actualtime * 60 - storedTime;
      if(remainingTime < 0){
        remainingTime = Actualtime * 60
      }
      this.wobblerTiming.startTime(remainingTime);
    });
  }

  triggerNextWobbler() {
    //Reset Time
    this.storageService.setValue(AppConstants.WOBBLER_CONSTANTS.WOBBLER_TIMING, "0");
    //Initialize next wobbler time
    this.wobblerLoc = this.wobblerLoc + 1;
    if (this.wobblersArray[this.wobblerLoc] !== undefined) {
      this.startWobllerTiming();
    }else{
      this.currentRoundTimeBasedWobblersCompleted = true;
    }
  }

}
