import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SplashComponent } from './splash/splash.component';
import { LogoutComponent } from './logout/logout.component';
import { ModelLoaderService } from '../model/model-loader.service';
import { BootstrapService } from './bootstrap.service';
import { IsomerCoreModule, JsCalcConnectorService } from '@btsdigital/ngx-isomer-core';

import { ComponentsModule } from '../components/components.module';
import { LoggerService as PulseLoggerService } from '@btsdigital/pulseutilities';
import { DropDownComponent } from './drop-down/drop-down.component';
import { TextEngineService } from './textengine/textengine.service';
import { NavigationService } from './navigation.service';

import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { AlertModule } from 'ngx-bootstrap/alert';
import { ModalModule } from 'ngx-bootstrap/modal';
import { AccordionModule } from 'ngx-bootstrap/accordion';
import { ButtonsModule } from 'ngx-bootstrap/buttons';
import { CarouselModule } from 'ngx-bootstrap/carousel';
import { TabsModule } from 'ngx-bootstrap/tabs';
import { TooltipModule } from 'ngx-bootstrap/tooltip';
import { PopoverModule } from 'ngx-bootstrap/popover';

import { CommonModalComponent } from './common-modal/common-modal.component';
import { ConfirmationPopup } from './confirmation-popup/confirmation-popup';
import { HelpComponent } from './help/help.component';
import { NetworkErrorComponent } from './network-error/network-error.component';
import { PasswordComponent } from './password-popup/password.component';
import { CalcDebuggerComponent } from './calc-debugger/calc-debugger.component';
import { FormsModule } from '@angular/forms';
import { A11yModule } from '@angular/cdk/a11y';
import { AlreadyLoggedInComponent } from "./already-logged-in/already-logged-in.component";
import { Utils } from './utils.service';
@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IsomerCoreModule,
    BsDropdownModule.forRoot(),
    AlertModule.forRoot(),
    ModalModule.forRoot(),
    AccordionModule.forRoot(),
    ButtonsModule.forRoot(),
    CarouselModule.forRoot(),
    TabsModule.forRoot(),
    TooltipModule.forRoot(),
    PopoverModule.forRoot(),
    ComponentsModule,
    A11yModule
  ],
  declarations: [
    SplashComponent,
    LogoutComponent,
    DropDownComponent,
    CommonModalComponent,
    ConfirmationPopup,
    HelpComponent,
    NetworkErrorComponent,
    PasswordComponent,
    CalcDebuggerComponent,
    AlreadyLoggedInComponent
  ],
  providers: [
    ModelLoaderService,
    BootstrapService,
    PulseLoggerService,
    TextEngineService,
    JsCalcConnectorService,
    NavigationService,
    Utils
  ],

  exports: [
    SplashComponent,
    LogoutComponent,

    IsomerCoreModule,
    ComponentsModule,

    AlertModule,
    BsDropdownModule,
    ModalModule,
    AccordionModule,

    ButtonsModule,
    CarouselModule,
    TabsModule,

    TooltipModule,
    PopoverModule,
    DropDownComponent,

    CommonModalComponent,
    ConfirmationPopup,
    HelpComponent,
    NetworkErrorComponent,
    PasswordComponent,
    CalcDebuggerComponent,
    AlreadyLoggedInComponent
  ]
})
export class SharedModule { }
