import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import * as _ from 'lodash';
import { TextEngineService } from '../../shared/textengine/textengine.service';

@Component({
  selector: 'isma-info-update',
  templateUrl: './info.component.html',
  styleUrls: ['./info.component.styl']
})
export class InfoComponent implements OnInit {

  @Input() data: any;
  @Input() rangeref: string;

  id: string;
  scene: any;

  @Output() continue: EventEmitter<any> = new EventEmitter<any>();
  valueToSet;

  constructor(private calcService: CalcService, private textHelper: TextEngineService) { }

  ngOnInit() {
    if (this.data) {
      this.id = this.data.ID;
      this.scene = this.data;

      this.appendConditionalText()
    }
  }

  onSubmit() {
    this.valueToSet = this.valueToSet != undefined ? this.valueToSet : 1;
    this.calcService.setValue(this.data.wobblerRange, this.valueToSet);
    this.continue.emit()
  }


  // Picking logic from old wobbler in backup folder
  appendConditionalText() {
    let i = 1;
    while (this.data["condition" + i + "rangename"] != undefined) {
      let conditionValue = this.calcService.getValue(this.data["condition" + i + "rangename"]);
      if (JSON.parse(conditionValue) == true) {
        this.valueToSet = this.data["alt" + i + "_value"]
        this.data.conditionalText = "";
        this.data.conditionalText += "<br/><br/>" + this.data["alt" + i + "_narrative"]
      }
      i++;
    }
  }
}
