import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'ism-drop-down',
  templateUrl: './drop-down.component.html',
  styleUrls: ['./drop-down.component.styl']
})
export class DropDownComponent implements OnInit {
  @Input() items:Array<string>
  @Input() title:string;
  @Input() initialIndex:number;
  @Output() itemChanged=new EventEmitter<number>();

  selectedItem:string;
  constructor() { }

  ngOnInit() {
    if(this.initialIndex==-1){
      this.selectedItem=this.title
    }else{
      this.selectedItem=this.items[this.initialIndex];
    }
  }

  dropdownChanged(index:number){
      this.selectedItem=this.items[index];
      this.itemChanged.emit(index); 
  }

  getSelectedItem():string{
    return this.selectedItem
  }

  resetToDropDownTitle(){
    this.selectedItem=this.title;
  }

  setDropDownValue(value:string){
    this.selectedItem=value;
  }
}
