import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
@Injectable()
export class Utils {
    public _defaultModel: any;
    modelLoadingPromise: any;

    focusElement: any;
    public get getFocusElement(): any {
        return this.focusElement;
    }
    public set setFocusElement(v: any) {
        this.focusElement = v;
    }

    userObj: any;
    public get getUserObj(): any {
        return this.userObj;
    }
    public set setUserObj(v: any) {
        this.userObj = v;
    }

    constructor(
        private router: Router,
        public calcService: CalcService,
        private communicator: CommunicatorService
    ) { }

    dictToJson(dict) {
        let jsonObj = {}
        dict.forEach((key, value) => {
            let temp = {}
            Object.entries(value.table).forEach(([key, value]) => {
                temp[value['key']] = value['value'];
            }
            );
            jsonObj[value.getValue('ID')] = temp;
        })

        return jsonObj;
    }

    getCharFromCode(code: number, base: number): string {
        if (base) {
            code += base;
        }
        return String.fromCharCode(code);
    }

    isRouteActive(route, url): Boolean {
        if (url.indexOf(route) == -1) {
            return false;
        } else {
            return true;
        }
    }

    getPristineModel(): Promise<CalcService> {
        if (this._defaultModel) {
            return Promise.resolve(this._defaultModel);
        } else if (this.modelLoadingPromise === undefined) {
            return this.modelLoadingPromise = this.getCalcService();
        } else {
            return this.modelLoadingPromise;
        }
    }

    getCalcService() {
        return this.calcService.buildModel().then((calcApi: any) => {
            this._defaultModel = calcApi;
            return calcApi;
        }).catch((err) => {
            console.log('err in utils getCalcService', err);
        });
    }

    forceResize() {
        setTimeout(() => {
            if (typeof (Event) === 'function') {
                // modern browsers
                window.dispatchEvent(new Event('resize'));
            } else {
                // for IE and other old browsers
                // causes deprecation warning on modern browsers
                //   var evt = window.document.createEvent('UIEvents'); 
                //   evt.initUIEvent('resize', true, false, window, 0); 
                //   window.dispatchEvent(evt);
            }
        }, 1000);
    }
}

