import { AfterViewInit, Component, Input, OnDestroy, OnInit, Renderer2, ViewChild } from '@angular/core';
import { CalcService, IsomerXapiService, NumberFormatting } from '@btsdigital/ngx-isomer-core'
import { NgxNouiSliderComponent } from 'ngx-noui-angular-slider';
import * as noUiSlider from 'nouislider';
import { value } from 'numeral';
import { Subscription } from 'rxjs';

@Component({
  selector: 'isma-calc-slider',
  templateUrl: './calc-slider.component.html',
  styleUrls: ['./calc-slider.component.styl']
})
export class CalcSliderComponent implements OnInit, OnDestroy {
  
  @Input() ref: string;
  @Input() name:string;
  @Input() yearRef: string;
  @Input() step: number;
  @Input() min: number = 0;
  @Input() max: number = 9999999999999999;
  @Input() showButtons: boolean = false;
  @Input() format: string;
  @Input() scaler: number = 1;
  @Input() xapiTrackingOnPulse: boolean = false;
  @Input() xapiTrackingOnLoad: boolean = null;
  private value: string | number;
  private tempValue: string;
  private year: string;
  private isDirty: boolean = false;

  subscription: Subscription;
  config: noUiSlider.Options;
  @ViewChild('nouiSliderEl', { static: true}) nouiSliderElRef: NgxNouiSliderComponent
  @ViewChild('nouiSliderEl', { static: true}) nouiSliderEl

  
  constructor(
    private calcService: CalcService, 
    private isomerXapiService: IsomerXapiService,
    private renderer:Renderer2
  ) { }

  ngOnInit() {
    this.value = this.calcService.getValueForYear(this.ref, this.yearRef);
    this.value = NumberFormatting.unformat(this.value);

    this.config = {
      start: [Number(this.value) || Number(this.min)],
      range: {
        'min': Number(this.min),
        'max': Number(this.max)
      },
      step: (this.step) ? Number(this.step) : undefined,
      pips: {
        mode: noUiSlider.PipsMode.Steps,
        density: 1
      },
      // animate: ,
      behaviour: 'drag-tap',
      keyboardSupport: true,
      keyboardDefaultStep: 1,
      format: {
        to: (value) => {
          return NumberFormatting.format(value, this.format, this.scaler);
        },
        from: (value) => {
          return +NumberFormatting.unformat(value, this.scaler);
        }
      }
    };

    // track data on load
    this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(this.ref),
    this.calcService.getRefNameByYearRef(this.ref, this.yearRef), String(this.value),
    this.yearRef ? this.calcService.getValue(this.yearRef) : this.calcService.getRoundValue(), 'calc-slider', this.xapiTrackingOnLoad, this.xapiTrackingOnPulse);


    this.subscription = this.calcService.getObservable().subscribe(() => {
        this.onModelChange();
    });


  }

  onSet(){
    const curVal = this.nouiSliderElRef.get()
    this.saveDataToModel(curVal);
  }

  saveDataToModel(val: any) {
    this.calcService.setValueForYear(this.ref, val, this.yearRef, 'calc-slider', this.xapiTrackingOnPulse);
  }

  onModelChange(){
    this.value = this.calcService.getValueForYear(this.ref, this.yearRef);
    this.value = NumberFormatting.unformat(this.value);

    const curVal = this.nouiSliderElRef.get()
    if(curVal != this.value){
      this.nouiSliderElRef.set(Number(this.value))
    }
  }

  ngOnDestroy() {
    if(this.subscription){
      this.subscription.unsubscribe();
    }
  }

  ngAfterViewInit(){
    if(this.name){
      this.renderer.setAttribute(this.nouiSliderElRef.target.querySelector('.noUi-handle'),'aria-label',this.calcService.getValue(this.name));
    }
  }
}
