  ngOnInit() {
    this.initializeSubscribersData();
    this.initializeBaseAndRenewalData();
    this.initializeChartTitles();
    this.chartOptions = {
      chart: {
        height: 240
      },
      legend: {
        align: 'right',
        verticalAlign: 'middle',
        layout: 'vertical',
        labelFormatter: function () {
          let words = this.name.split(/[\s]+/);
          let numWordsPerLine = 2;
          let str = [];
          for (let i = 0; i < words.length; i++) {
            if (i > 0 && i % numWordsPerLine == 0)
              str.push('<br>');
            str.push(words[i]);
          }
          return str.join(' ');
        }
      },
      'plotOptions': {
        'column': {
          'dataLabels': {
            enabled: false
          },
          'tooltip': {
            'pointFormat': '{point.y:$0,0am}'
          }
        }
      }
    }
  }

