import { Component, OnInit } from '@angular/core';
import { Constants } from '../../config/constants';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';
import { NavigationService } from '../../shared/navigation.service';

const ICONS_PATH="../../../assets/icons/";

@Component({
  selector: 'ism-intro',
  templateUrl: './intro.component.html',
  styleUrls: ['./intro.component.styl']
})
export class IntroComponent implements OnInit {
  teamName: string = "Team Name";
  teamNumber: string = "Team Number";
  roundNumber: string = "1";
  constructor(private router: Router,private navigationService:NavigationService,private textService:TextService,private calcService:CalcService) { }
  decisions = [];
  reports = [];

  ngOnInit() {
    this.initializeDecisions();
    this.initializeReports();
  }

  initializeDecisions(){
    this.decisions = this.navigationService.getDecisions().slice(1);
  }

  initializeReports(){
    this.reports = this.navigationService.getReports();
  }
  

}
