import { Component, Input, OnInit, Output, EventEmitter, HostListener } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subject } from 'rxjs';
import * as _ from 'lodash';
import { TextEngineService } from '../../shared/textengine/textengine.service';

@Component({
  selector: 'isma-single-select',
  templateUrl: './single-select.component.html',
  styleUrls: ['./single-select.component.styl']
})
export class SingleSelectComponent implements OnInit {

  @Input() data: any;
  @Input() rangeRef: string;
  @Input() isDisabled = false;

  choices: any[] = [];
  answer = 0;
  id: string;
  scene: any;
  public readyToSubmit = false;

  @Output() selectedAnswer: Subject<any> = new Subject<any>();
  @Output() submitted = new EventEmitter()

  @HostListener('document:keyup.arrowup', ['$event'])
  @HostListener('document:keyup.arrowdown', ['$event'])
  @HostListener('document:keyup.arrowleft', ['$event'])
  @HostListener('document:keyup.arrowright', ['$event'])
  public handleKeyboardEvent(event) {
    switch (event.keyCode) {
      case 39:
      case 40:
        $('.choice:focus').next().focus();
        event.preventDefault();
        event.stopPropagation();
        event.stopImmediatePropagation();
        break;
        case 37:
        case 38:
          $('.choice:focus').prev().focus();
          event.preventDefault();
          event.stopPropagation();
          event.stopImmediatePropagation();
        break;
    }
  }

  constructor(
    private calcService: CalcService, 
    private textHelper: TextEngineService
  ) { }

  ngOnInit() {
    if (this.data) {
      this.id = this.data.ID;
      this.scene = this.data;


      const curVal = this.calcService.getValue(this.rangeRef);

      if (curVal != "0") {
        // Answer is stored as W1A,W1B,W1C in model where as used as 1,2,3,... in html/ts
        this.answer = curVal.charCodeAt(curVal.length - 1) - 64;
      }

      this.choices = this.textHelper.getSingleSelectChoices(this.id);
    }
  }

  onSelect(val) {
    this.readyToSubmit = true;
    //In case some component needs to listen to change
    this.selectedAnswer.next(this.answer);
    this.answer = val
  }

  onSubmit(){
    const val = this.data.prefix + String.fromCharCode(64 + (+this.answer))
    this.calcService.setValue(this.rangeRef, val);
    this.submitted.emit()
  }
}
