import { Component, OnInit, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { StorageService, CalcService, TextService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { NavigationEnd, Router } from '@angular/router';
import { CommonModalComponent } from '../../shared/common-modal/common-modal.component';
import { ConfirmationPopup } from '../../shared/confirmation-popup/confirmation-popup';
import { Constants } from '../../config/constants';
import { filter } from 'rxjs/operators';

@Component({
  selector: 'ism-submit-menu',
  templateUrl: './submit-menu.component.html',
  styleUrls: ['./submit-menu.component.styl']
})
export class SubmitMenuComponent implements OnInit {
  menuItems = ['Logout', 'HelpMenuItem','SubmitDecisions', 'FacilitatorMenu'];
  errorMessageKey='';

  @ViewChild('submitModal', { static: false}) public submitModalRef: ConfirmationPopup;
  @ViewChild('logoutModal', { static: false}) public logoutModalRef: ConfirmationPopup;
  @ViewChild('errorModal', { static: false}) public ErrorModalRef: CommonModalComponent;

  constructor(
    private storageService: StorageService, 
    private communicator: CommunicatorService, 
    private router: Router, 
    private calcService: CalcService,
  ) { }

  ngOnInit() {
    if(this.router.url == '/intro' || this.router.url == '/holdScreen' || this.router.url == '/registration'){
      let index = this.menuItems.indexOf('SubmitDecisions');
      this.menuItems.splice(index,1);
    }
  }

  menuItemClicked(index) {
    if(index==0){
      this.logoutModalRef.showAlert()
    }else if(index==1){
      this.communicator.trigger(Constants.EVENTS.RAISE_HELP)
    }else if(index==2){
      this.showSubmitModal()
    }else if(index == 3){
      this.communicator.trigger(Constants.EVENTS.MANUALLY_OPEN_DEBUGGER)
    }
  }
  
  logout(){
    this.router.navigateByUrl("/logout")
  }

  showSubmitModal() {
    let error = this.checkForErrors();
    if (error) {
      this.ErrorModalRef.openModal()
    }else{
      this.submitModalRef.showAlert();
    }
  }

  checkForErrors() {
    let FTEs = +this.calcService.getValue("DB_Dash7Raw");
    if (FTEs < 0) {
      this.errorMessageKey="ErrorCheck7Text"
      return true;
    }
    return false;
  }

  nextRound() {
    let currentRound = this.calcService.getValue("tlInputTeamRound");
    // if (currentRound == '3') {
    //   this.storageService.setValue("wobblersCompleted", 'true');
    // };
    let key = +currentRound == 1 ? Constants.EVENTS.ROUND_ONE_COMPLETE :
              +currentRound == 2 ? Constants.EVENTS.ROUND_TWO_COMPLETE :
              +currentRound == 3 ? Constants.EVENTS.ROUND_THREE_COMPLETE : Constants.EVENTS.INTRO_COMPLETE;
    Promise.all([
      // this.storageService.setValue("currentRound",currentRound),
      this.storageService.setValue(key, 'true'),      
      // this.storageService.setValue("HOLDSCREEN","true"),
      this.calcService.setValue("Delivery_DirectorR"+currentRound,1)
    ]).then(()=>{
      // this.submitModalRef.closeModal();
      this.router.navigate(['/holdScreen']);
    })
  }
}
