import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BootstrapService } from '../bootstrap.service';
import { StorageService, Constants, CommunicatorService, CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { LoggerService } from '@btsdigital/pulseutilities';
import { Constants as AppConstants } from 'src/app/config/constants';

@Component({
  selector: 'ism-splash',
  templateUrl: './splash.component.html',
  styleUrls: ['./splash.component.styl']
})
export class SplashComponent implements OnInit {

  private modelProgressSubscription: Subscription;
  progress = 0;

  constructor(
    private bootstraper: BootstrapService, private router: Router,
    private logger: LoggerService, private storageService: StorageService,
    private communicator: CommunicatorService, private calcService: CalcService) { }

  ngOnInit() {
    if (!this.bootstraper.appReady) {
      this.modelProgressSubscription = this.communicator
        .getEmitter(Constants.MODEL_LOAD_PROGRESS)
        .subscribe((progress) => this.progress = progress);

      this.bootstraper
        .init()
        .then(() => {
          this.appReady();
        });
    }
  }

  // private appReady() {
  //   this.logger.log('Model loaded');
  //   let returnUrl: string;
  //   this.storageService.getValue('HOLDSCREEN')
  //     .then((value)=>{
  //       if(value=='true'){
  //         this.router.navigate(['/holdScreen']);
  //       } else {
  //         this.storageService.getValue(Constants.RETURN_URL)
  //           .then(val => {
  //             returnUrl = ((val) ? val : '/registration') + '';
  //             this.router
  //               .navigateByUrl(returnUrl);
  //           })
  //       }
  //     })
  //     .catch(err => {
  //       this.router.navigateByUrl('');
  //     });
  // }

  private appReady() {
    this.logger.log('Model loaded');
    let returnUrl: string;
    this.ensureRoundCompleted().then((roundCompleted)=>{
      if(roundCompleted){
        this.router.navigate(['/holdScreen']);
      }else{
        this.ensureRoundStarted().then((roundStarted)=>{
          if(roundStarted){
            this.storageService.getValue(Constants.RETURN_URL)
            .then(val => {
              returnUrl = ((val && val != 'holdScreen') ? val : '/intro') + '';
              this.router.navigateByUrl(returnUrl);
            })
          }else{
            this.router.navigate(['/registration']);
          }
        })
      }
    })
  }

  ensureRoundStarted(): Promise<any> {
    let round = +this.calcService.getValue('tlInputTeamRound');
    let key = round == 1 ? AppConstants.EVENTS.ROUND_ONE_STARTED : 
              round == 2 ? AppConstants.EVENTS.ROUND_TWO_STARTED : 
              round == 3 ? AppConstants.EVENTS.ROUND_THREE_STARTED: 
              AppConstants.EVENTS.INTRO_COMPLETE;


    return new Promise((resolve, reject) => {
      this.storageService.getValue(key)
        .then((started: string) => {
          if (started === 'true') {
            resolve(true);
          } else {
            resolve(false);
          }
        });
    });
  }

  ensureRoundCompleted(): Promise<any> {
    let round = +this.calcService.getValue('tlInputTeamRound');
    let key = round == 1 ? AppConstants.EVENTS.ROUND_ONE_COMPLETE : 
              round == 2 ? AppConstants.EVENTS.ROUND_TWO_COMPLETE : 
              round == 3 ? AppConstants.EVENTS.ROUND_THREE_COMPLETE: 
              AppConstants.EVENTS.INTRO_COMPLETE;


    return new Promise((resolve, reject) => {
      this.storageService.getValue(key)
        .then((completed: string) => {
          if (completed === 'true') {
            resolve(true);
          } else {
            resolve(false);
          }
        });
    });
  }
}
