import { Injectable, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService, Constants, HttpWrapperService, ManifestService, StorageService, SyncService, SyncStatusService, TextService } from '@btsdigital/ngx-isomer-core';
import { SignalRService } from '@btsdigital/pulsesignalr';
import { LoggerService } from '@btsdigital/pulseutilities';
import { ModelLoaderService } from '../model/model-loader.service';
import { TextEngineService } from './textengine/textengine.service';
import { NavigationService } from './navigation.service';
import { AppConfig } from '../app.config';
import { Constants as AppConstants } from '../config/constants'
import { Utils } from './utils.service';

window['__theme'] = 'bs4';
@Injectable()
export class BootstrapService {

  // private modelLoadProgressSub: Subscription;
  private textServiceInitPromise: Promise<any>;
  private modelLoadCompletePromise: Promise<any>;
  private isAppReady = false;
  private textLoadedPromise:Promise<any>;
  isInitialized = false;
  userObj: any;
  
  // private testNotify = new notifyComponent();
  constructor(private calcService: CalcService, private textService: TextService,
    private storageService: StorageService, private httpWrapper: HttpWrapperService,private textEngineService:TextEngineService,
    private communicator: CommunicatorService, private logger: LoggerService,
    private router: Router, private modelLoader: ModelLoaderService, private signalR: SignalRService, private syncService: SyncService,
    private manifestService: ManifestService, private httpWrapperService: HttpWrapperService, private syncStatusService: SyncStatusService,
    private navigationService:NavigationService, private appConfig: AppConfig, private utils: Utils) { 
      this.userObj = JSON.parse(localStorage.getItem('user'));
      this.utils.setUserObj = this.userObj;
    }

  initAppConfig(){
    this.logger.isLogging = this.appConfig.configuration.logging;
    this.manifestService.setConfig({ config: this.appConfig.configuration });
    this.httpWrapperService.setHostName(this.appConfig.configuration.hostName);
    this.signalR.configureSignalRService({ hostname: this.appConfig.configuration.hostName, serviceUrl: this.appConfig.configuration.serviceUrl, hasHeartBeat: true });
    this.isInitialized = true;
  }
  init(): Promise<any> {
    if(!this.isInitialized){
      this.initAppConfig();
    }
    return this.modelLoader.getModel().then((response) => {
      const model = response;

      // set theme mode
      // set storage mode
      this.storageService.setMode(this.appConfig.configuration.connectToPulse ? Constants.STORAGE_MODES.MIXED : Constants.STORAGE_MODES.LOCAL);
      // set initial language to be loaded
      this.textService.language = 'en';
     

      // load text content
      this.textServiceInitPromise = this.textService.init();
      // try and load the calc model
      this.modelLoadCompletePromise = this.calcService
        .getApi(model)
        .catch((err) => {
          this.logger.log('Error when loading model ', err);
        });
      this.textLoadedPromise = this.textEngineService.init();
      return Promise.all([this.textServiceInitPromise, this.modelLoadCompletePromise,this.textLoadedPromise])
        .then(() => {
          this.navigationService.init()
          if (this.appConfig.configuration.connectToPulse === true) {
            this.syncStatusService.SetModelStateStatus(0);
            return this.syncService.initializeSync().then(() => {
              return this.syncService.setMode(Constants.CONNECTION_MODE.PUSH).then(()=>{
                this.appReady = true;
              });
            });
          }else{
            this.appReady = true
          }
        });
    });
  }

  // private onProgress(progress: string) {
  //   this.logger.log('Progress ' + progress);
  // }

  public set appReady(v: boolean) {
    this.isAppReady = v;
    this.communicator.trigger(AppConstants.APP_READY, v);
  }

  public get appReady(): boolean {
    return this.isAppReady;
  }
}
