import { Component, OnInit } from '@angular/core';
import { TextEngineService } from '../../shared/textengine/textengine.service';
import { CalcService } from '@btsdigital/ngx-isomer-core';

const MAX_CHECKED_INITIATIVES = 2;
const INITIATIVE_RANGENAME_PREFIX = 'tlInputSI0';
@Component({
  selector: 'ism-strategic-initiatives',
  templateUrl: './strategic-initiatives.component.html',
  styleUrls: ['./strategic-initiatives.component.styl']
})

export class StrategicInitiativesComponent implements OnInit {
  initiatives = [];
  selectedOptions=[];
  totalChecked = 0;
  maxChecks = MAX_CHECKED_INITIATIVES;
  
  constructor(private textEngineService: TextEngineService, private calcService: CalcService) { }

  ngOnInit() {
    this.initalizeInitiativesArray();
    this.sortInitiativeArrayBasedOnSerialNumber();
    this.updateAlreadyCheckedInitiatives();
    this.disableInitiativesCheckedInPreviousRound()
  }

  initalizeInitiativesArray() {
    let initiativesObject = this.textEngineService.getInitiatives();
    for (let key in initiativesObject) {
      if (initiativesObject.hasOwnProperty(key)) {
        this.setupAndAddToInitiativesArray(initiativesObject[key])
      }
    }
  }


  sortInitiativeArrayBasedOnSerialNumber(){
    this.initiatives.sort((a,b)=>{
      return a.serialNumber-b.serialNumber;
    })
  }

  setupAndAddToInitiativesArray(initiativeObject) {
    initiativeObject.isChecked = false;
    initiativeObject.isEnabled = true;
    initiativeObject.serialNumber = this.getSerialNumberFromID(initiativeObject.ID);
    this.initiatives.push(initiativeObject);
  }


  getSerialNumberFromID(id: string) {
    return id.replace("Initiative", "");
  }

  toggleInitiativeChecked(initiative) {
    if (initiative.isEnabled) {
      initiative.isChecked = !initiative.isChecked;
      return true
    }else{
      return false;
    }
  }

  updateEnabledInitiatives() {
    for (let i = 0; i < this.initiatives.length; i++) {
      this.initiatives[i].isEnabled = (
        (!this.initiatives[i].isLocked) &&
        ((this.initiatives[i].isChecked == 1) ||
          ((this.initiatives[i].isChecked == 0) && (this.totalChecked < MAX_CHECKED_INITIATIVES))));
    }
  }

  setModelValues() {
    this.resetModelValues()
    for (let i = 0; i < this.selectedOptions.length; i++) {
      let rangeName=INITIATIVE_RANGENAME_PREFIX+(i+1)
      let selectedInitiative=this.initiatives[+this.selectedOptions[i]-1];
      this.calcService.setValue(rangeName, selectedInitiative.serialNumber);
    }
  }

  resetModelValues() {
    for (let i = 1; i <= MAX_CHECKED_INITIATIVES; i++) {
      this.calcService.setValue(INITIATIVE_RANGENAME_PREFIX + i, 0);
    }
  }

  setupSelectedInitiativesDetails(){
    this.totalChecked = 0;
    this.selectedOptions=[]
    for (let i = 0; i < this.initiatives.length; i++) {
      if (this.initiatives[i].isChecked && this.initiatives[i].isEnabled) {
        this.totalChecked++;
        this.selectedOptions.push(this.initiatives[i].serialNumber)
      }
    }
  }

  selectInitiative(event, index) {
    if (event) {
      event.stopPropagation()
    }
    let valueChanged = this.toggleInitiativeChecked(this.initiatives[index]);
    if(valueChanged){
      this.setupSelectedInitiativesDetails()
      this.updateEnabledInitiatives();
      this.setModelValues();
    }
  }

  updateAlreadyCheckedInitiatives(){
    for (let i = 1; i <= MAX_CHECKED_INITIATIVES; i++) {
      let checkedInitIndex=+this.calcService.getValue(INITIATIVE_RANGENAME_PREFIX + i);
      if(checkedInitIndex!=0){
        this.initiatives[checkedInitIndex-1].isChecked=true;
      }
    }
    this.setupSelectedInitiativesDetails();
    this.updateEnabledInitiatives();
  }

  disableInitiativesCheckedInPreviousRound(){
    let currentRound=+this.calcService.getValue("tlInputTeamRound");
    if(currentRound==1){
      return;
    }
    for(let i=2;i<=currentRound;i++){
      for(let j=1;j<=2;j++){
        let selectedInitiative=+this.calcService.getValue("tlOutput_init0"+j+"R"+(i-1));
        if(selectedInitiative!=0){
          this.initiatives[selectedInitiative-1].isLocked=true;
          this.initiatives[selectedInitiative-1].isChecked=true;
        }
      }
    }
    this.updateEnabledInitiatives();
  }

  handleKeyDown(event: KeyboardEvent){
    if (event.keyCode === 13 || event.keyCode === 32) {
      event.stopImmediatePropagation();
      event.stopPropagation();
      event.preventDefault();
    }
  }

  handleKeyUp(event, index){
    // space or enter
    if (event.keyCode === 13 || event.keyCode === 32) {
      this.selectInitiative(event, index)
  }
  }

  handleKeyboardEvent(event: KeyboardEvent, actionType: string, ...args) {
    if (event.keyCode === 13 || event.keyCode === 32) {
        event.preventDefault();
        event.stopPropagation();
        switch (actionType) {
            case 'checkInitiative':
                this.selectInitiative(event,args[0])
                break;
        }
    }
}
}
