import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CalcModelLoadedGuard } from '@btsdigital/ngx-isomer-core';
import { RegistrationComponent } from './registration/registration.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { IntroComponent } from './intro/intro.component';
import { ForecastingComponent } from './forecasting/forecasting.component';
import { GoToMarketComponent } from './go-to-market/go-to-market.component';
import { ProductDevlopmentComponent } from './product-devlopment/product-devlopment.component';
import { StrategicInitiativesComponent } from './strategic-initiatives/strategic-initiatives.component';
import { SdppComponent } from './sdpp/sdpp.component';
import { DecisionSummaryComponent } from './decision-summary/decision-summary.component';
import { PnlComponent } from './pnl/pnl.component';
import { HoldScreenComponent} from './holdscreen/holdscreen.component'

const routes: Routes = [
  {
    path: 'registration',
    component: RegistrationComponent,
    canActivate: [CalcModelLoadedGuard]
  }, {
    path: 'dashboard',
    component: DashboardComponent,
    canActivate: [CalcModelLoadedGuard],
    data: {
      hideInDebugger: true
    },
    children: [
      {
        path: '',
        redirectTo: '/forecasting',
        pathMatch: 'full',
        data:{
          hideInDebugger: true
        },
      },
      {
        path: 'forecasting',
        component: ForecastingComponent,
        canActivate: [CalcModelLoadedGuard]
      },
      {
        path: 'gtm',
        component: GoToMarketComponent,
        canActivate: [CalcModelLoadedGuard]
      }, 
      {
        path: 'prod-dev',
        component: ProductDevlopmentComponent,
        canActivate: [CalcModelLoadedGuard]
      },
      {
        path: 'sdpp',
        component: SdppComponent,
        canActivate: [CalcModelLoadedGuard]
      },
      {
        path: 'initiatives',
        component: StrategicInitiativesComponent,
        canActivate: [CalcModelLoadedGuard]
      },
      {
        path: 'decision-summary',
        component: DecisionSummaryComponent,
        canActivate: [CalcModelLoadedGuard]
      },
      {
        path: 'pnl',
        component: PnlComponent,
        canActivate: [CalcModelLoadedGuard]
      }
    ]
  }, 
  {
    path: 'intro',
    component: IntroComponent,
    canActivate: [CalcModelLoadedGuard]
  },
  {
    path:'holdScreen',
    component: HoldScreenComponent,
    canActivate:[CalcModelLoadedGuard]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ViewsRoutingModule { }
