import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpWrapperService } from '@btsdigital/ngx-isomer-core';
import { AppConfig } from 'src/app/app.config';

@Component({
  selector: 'ism-logout',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.styl']
})
export class LogoutComponent implements OnInit, OnDestroy {

  constructor(
    private httpWrapper: HttpWrapperService,
    private appConfig: AppConfig
  ) { }

  ngOnInit() {
    const logoutUrl = (this.appConfig.configuration.logoutUrl) ? (this.appConfig.configuration.logoutUrl) :
      (this.appConfig.configuration.CDN0LOCAL1LOCALHOST2 === 0) ? '/Wizer/CloudFront/Logout' : '/Wizer/Wizer/Logout';
    this.httpWrapper.postJson(logoutUrl, null)
      .then(result => {
        // clear all cookies
        const cookies = document.cookie.split(';');
        for (let i = 0; i < cookies.length; i++) {
          const cookie = cookies[i].split('=')[0].trim();
          if (cookie.length > 0) {
            document.cookie = cookie + '=; expires=Thu, 01 Jan 1970 00:00:00 UTC';
          }
        }
        setTimeout(() => {
          const url = this.appConfig.configuration.redirectAfterlogoutUrl;
          if (this.appConfig.configuration.redirectAfterlogoutUrl) {
            window.location.href = url;
          } else {
            if (this.appConfig.configuration.CDN0LOCAL1LOCALHOST2 === 1) {
              window.location.href = window.location.protocol + '//' + window.location.hostname + '/sim/login/';
            } else {
              window.location.href = window.location.protocol + '//' + window.location.hostname +
                ((window.location.port) ? ':' + window.location.port : '') + '?eraseCache=true/';
            }
          }
        }, 500);
      });
  }


  ngOnDestroy() {

  }
}
