import { Component, OnInit, Input, ViewEncapsulation, ViewChild, ElementRef, HostListener } from '@angular/core';
import * as _ from 'lodash';
import { CalcService, TextService, StorageService } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';
import { Constants } from '../../config/constants';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { NavigationService } from '../../shared/navigation.service';

// const KEY_NUM_1 = 49;
// const KEY_NUM_4 = 52;
@Component({
  selector: 'ism-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.styl'],
})

export class DashboardComponent {
  pageTitleRef: string;
  decisionDropDownDetails: { items: Array<string>, title: string, initialIndex: number };
  reportsDropDownDetails: { items: Array<string>, title: string, initialIndex: number };
  dashboardValuesModel: { rangeName: string, value: string, change: number }[] = []
  @ViewChild('decisionDropDown', { static: false}) decisionDropDown;
  @ViewChild('reportsDropDown', { static: false}) reportsDropDown;
  @ViewChild('pageDetailContainer', { static: false}) pageDetailContainerRef: ElementRef;

  @ViewChild('debuggerModal', { static: false}) public debuggerModalRef: ModalDirective;
  // @ViewChild('wobblers', { static: false}) public WoblersRef;
  teamName: string;
  // @HostListener('document:keyup', ['$event'])
  // onKeypress($event) {
  //   if ($event.keyCode === KEY_NUM_1 && $event.shiftKey) {
  //     this.WoblersRef.TriggerManualWobbler();
  //   } else if ($event.keyCode === KEY_NUM_4 && $event.shiftKey) {
  //     this.WoblersRef.overrideWobblers();
  //   }
  // }

  constructor(private router: Router, private navigationService: NavigationService, private textService: TextService, private calcService: CalcService, private storageService: StorageService) {

  }

  ngOnInit() {
    this.setPageTitleRef(this.router.url);
    this.initializeDecisionDropDownDetails();
    this.initializeReportDropDownDetails();
    this.setDropDownInitialIndex(this.router.url);
    this.initalizeDashboardValuesModel()
    this.calcService.getObservable().subscribe(() => {
      this.onModelChanged()
    });
  }

  initializeDecisionDropDownDetails() {
    this.decisionDropDownDetails = {
      items: this.navigationService.getDecisionDropDownTitles(),
      title: this.textService.getText("Decisions"),
      initialIndex: -1
    }
    this.teamName= this.calcService.getValue('tlInputTeamName')
  }

  initializeReportDropDownDetails() {
    this.reportsDropDownDetails = {
      items: this.navigationService.getReportsDropDownTitles(),
      title: this.textService.getText("Reports"),
      initialIndex: -1
    }
  }

  setPageTitleRef(currentRoute: string) {
    let navigationItem = this.navigationService.getNavigationItemByRoute(currentRoute);
    this.pageTitleRef = navigationItem.pageTitleRef
  }

  setDropDownInitialIndex(currentRoute: string) {
    let navigationItem = this.navigationService.getNavigationItemByRoute(currentRoute);
    if (navigationItem.type == Constants.PAGE_TYPE_DECISION) {
      this.decisionDropDownDetails.initialIndex = navigationItem.dropDownIndex;
    } else {
      this.reportsDropDownDetails.initialIndex = navigationItem.dropDownIndex;
    }
  }

  decisionItemChanged(index) {
    this.reportsDropDown.resetToDropDownTitle()
    let navigationItem = this.navigationService.getDecisionNavigationItemByIndex(index);
    this.setupDashboardAndNavigateToSelectedPage(navigationItem);
  }

  reportItemChanged(index) {
    this.decisionDropDown.resetToDropDownTitle();
    let navigationItem = this.navigationService.getReportNavigationItemByIndex(index);
    this.setupDashboardAndNavigateToSelectedPage(navigationItem);
  }

  setupDashboardAndNavigateToSelectedPage(navigationItem) {
    this.pageTitleRef = navigationItem.pageTitleRef;
    this.router.navigateByUrl(navigationItem.route);
    this.pageDetailContainerRef.nativeElement.scrollTop = 0;
  }

  initalizeDashboardValuesModel() {
    for (let i = 1; i <= 7; i++) {
      let rname = "DB_Dash" + i + "Raw"
      this.dashboardValuesModel.push({
        rangeName: rname,
        value: this.calcService.getValue(rname, true),
        change: 0
      })
    }
    this.dashboardValuesModel.push({
      rangeName: "DB_Dash3BRaw",
      value: this.calcService.getValue("DB_Dash3BRaw", true),
      change: 0
    }, {
      rangeName: "DB_Dash3SubRaw",
      value: this.calcService.getValue("DB_Dash3SubRaw", true),
      change: 0
    })
  }

  onModelChanged() {
    for (let i = 0; i <= 8; i++) {
      if (this.dashboardValuesModel[i].value != this.calcService.getValue(this.dashboardValuesModel[i].rangeName, true)) {
        this.dashboardValuesModel[i].change = +this.calcService.getValue(this.dashboardValuesModel[i].rangeName, true) - +this.dashboardValuesModel[i].value
        this.dashboardValuesModel[i].value = this.calcService.getValue(this.dashboardValuesModel[i].rangeName, true)
        setTimeout(() => {
          this.dashboardValuesModel[i].change = 0
        }, 3000)
      }
    }
  }

}
