import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { WobblerTimingService } from './wobbler-timing.service';
import { WobblerLibraryComponent } from './wobblerlibrary/wobblerlibrary.component';
import { SingleWobblerComponent } from './singlewobbler/singlewobbler.component';
import { SingleSelectComponent } from './single-select/single-select.component';
import { InfoComponent } from './info/info.component';
import { FeedbackwobblerComponent } from './feedbackwobbler/feedbackwobbler.component';
import { ReviewwobblerComponent } from './reviewwobbler/reviewwobbler.component';
import { IsomerCoreModule } from '@btsdigital/ngx-isomer-core';
import { SharedModule } from '../shared/shared.module';
import { FormsModule } from '@angular/forms';

const components = [
  WobblerLibraryComponent,
  SingleWobblerComponent,
  SingleSelectComponent,
  InfoComponent,
  FeedbackwobblerComponent,
  ReviewwobblerComponent
]

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IsomerCoreModule,
    SharedModule
  ],
  declarations: components,
  exports: components,
  providers: [WobblerTimingService]
})
export class WobblerModule { }
