import { Component, ViewChild } from '@angular/core';
import { CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { Constants } from './config/constants';
import { BootstrapService } from './shared/bootstrap.service';
import * as $ from 'jquery';
@Component({
  selector: 'ism-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.styl']
})
export class AppComponent {
  isAppReady: boolean = false;
  constructor(
    private bootstrapService: BootstrapService,
    private communicator: CommunicatorService
    ) {

  }
  ngOnInit() {
    this.bootstrapService.initAppConfig();
    this.communicator.getEmitter(Constants.APP_READY).subscribe((value)=>{
      if (value !== undefined) {
        this.isAppReady = ((value + '') === 'true') ? true : false;
      }
    })
  }
}
