  wobblerInputData(){
    let wobbler1= this.calcService.getValue('tlInputWobbler1');
    let wobbler2= this.calcService.getValue('tlInputWobbler2'); 

    this.showWobblerTable=  (this.currentRound=='1' || this.currentRound=='2') && wobbler1!='0' ? true: 
                            (wobbler1!='0' || wobbler2!='0') && this.currentRound=='3' ? true : false;
  }

  getSelectedInitiaitives() {
    let selectedInitiativeNames = ['', '']
    let selectedInitiativeIndexes=[this.calcService.getValue("tlOutput_C59_tlDecSummary"),this.calcService.getValue("tlOutput_C60_tlDecSummary")];
    let selectedInitiativesIDs = ["Initiative"+selectedInitiativeIndexes[0], "Initiative" + selectedInitiativeIndexes[1]];
    let initiatives = this.textEngineService.getInitiatives();
    selectedInitiativeNames[0] = (selectedInitiativeIndexes[0]=="0")?"0":selectedInitiativeIndexes[0]+": "+initiatives[selectedInitiativesIDs[0]].name
    selectedInitiativeNames[1] = (selectedInitiativeIndexes[1]=="0")?"0":selectedInitiativeIndexes[1]+": "+initiatives[selectedInitiativesIDs[1]].name
    return selectedInitiativeNames;
  }

  getSelectedWobblers() {  
    let result= [];
    let selectedWobblerIndexes= this.calcService.getValue("tlOutput_C64_tlDecSummary");
    let conditionalIndex= this.calcService.getValue("tlOutput_C66_tlDecSummary")

   // for wobbler of type singleSelect
    let wobblers = [];
    wobblers= this.textEngineService.getInitiatives('SingleSelect');
    let currentWobbler= Object.values(wobblers);
    currentWobbler= currentWobbler.filter(x=> x.round==this.currentRound);

    let id= selectedWobblerIndexes.split('')[2]
    let choiceId= id=="A"? 1: id=="B" ? 2: id=="C" ? 3: 4
    let choice= "alt"+choiceId+"_name"
    result[0]= currentWobbler[0][choice];

    // for wobbler of type ConditionalWobbler
    if(this.currentRound=='3'){
      let conditionalWobblers = [];
      conditionalWobblers= this.textEngineService.getInitiatives('ConditionalWobbler');
      let currentConditionalWobblers= Object.values(conditionalWobblers);
  
      let conditionalId= conditionalIndex.split('')[2]
      let conditionalChoiceId= conditionalId=="A"? 1: conditionalId=="B" ? 2: conditionalId=="C" ? 3: 4
      let conditionalChoice= "alt"+conditionalChoiceId+"_name"
      result[1]= conditionalIndex=='0' ? 0 : conditionalId+': '+currentConditionalWobblers[0][conditionalChoice];
    }

    return result;
  }
