import { Injectable, HostListener } from '@angular/core';
// import { setInterval } from 'timers';
import { Subject, Observable, interval } from 'rxjs';
import { StorageService } from '@btsdigital/ngx-isomer-core';
import { takeWhile } from 'rxjs/operators';
import { Constants as AppConstants } from '../config/constants';

@Injectable()
export class WobblerTimingService {
  private showWobbler = new Subject<any>();
  private secondsCount: number = 0;

  private active: boolean;

  constructor(private dataStore: StorageService) { }

  init() {
  }

  startTime(time){
    this.active = true;
    this.secondsCount = 0;
    console.log("NEW WOBBLER WILL TRIGGER IN " + time + "Seconds")
    interval(1000)
      .pipe(
        takeWhile(() => this.active)
      )
      .subscribe(() => {
        this.secondsCount++;
        if(this.secondsCount % 30 == 0){
          // save time every 30 seconds
          this.SaveTimer();
        }
        if (this.secondsCount >= time) {
          this.resetTimer();
          this.showWobbler.next();
        }
      });
  }

  SaveTimer() {
    this.dataStore.getValue(AppConstants.WOBBLER_CONSTANTS.WOBBLER_TIMING).then((res) => {
      // let storedTime = res && res != '' ? parseInt(res.toString()) : 0
      // this.dataStore.setValue(AppConstants.WOBBLER_CONSTANTS.WOBBLER_TIMING, (storedTime + this.secondsCount).toString());
      this.dataStore.setValue(AppConstants.WOBBLER_CONSTANTS.WOBBLER_TIMING, (this.secondsCount).toString());
    });
  }

  getObservable(): Observable<any> {
    return this.showWobbler.asObservable();
  }

  resetTimer() {
    this.active = false;
    this.dataStore.setValue(AppConstants.WOBBLER_CONSTANTS.WOBBLER_TIMING, "0");
  }
  
  // forceTrigger(){
  //   this.resetTimer();
  //   this.showWobbler.next();
  // }

  ngOnDestroy(){
    this.active = false;
  }
}
