import { Injectable } from '@angular/core';
import {Constants} from '../config/constants'
import { TextService, CalcService } from '@btsdigital/ngx-isomer-core';
const ICONS_PATH="./assets/icons/";


@Injectable()
export class NavigationService {
  private decisionsNavigationModel;
  private reportsNavigationModel;
  constructor(private textService:TextService,private calcService:CalcService) { }


  init(){
    this.decisionsNavigationModel=[
      {
        type:Constants.PAGE_TYPE_DECISION,//Because Introduction is shown in Decisions drop down
        route:Constants.INTRO_ROUTE,
        pageTitleRef:'',
        title:this.textService.getText("Introduction"),
        dropDownIndex:0
      },
      {
        type: Constants.PAGE_TYPE_DECISION,
        route: Constants.FORECASTING_ROUTE,
        pageTitleRef: Constants.FORECASTING_PAGE_TITLE_REF,
        dropDownIndex:1,
        img: ICONS_PATH+"Forecast_intro.png",
        title: this.calcService.getValue('tlOutput_C1_tlForecasting')
      },
      {
        type: Constants.PAGE_TYPE_DECISION,
        route: Constants.PROD_DEV_ROUTE,
        pageTitleRef: Constants.PROD_DEV_PAGE_TITLE_REF,
        dropDownIndex:2,
        img: ICONS_PATH+"Development_icon.png",
        title: this.calcService.getValue('tlOutput_C1_tlProdDev')
      },
      {
        type: Constants.PAGE_TYPE_DECISION,
        route: Constants.GTM_ROUTE,
        pageTitleRef: Constants.GTM_PAGE_TITLE_REF,
        dropDownIndex:3,
        img: ICONS_PATH+"Go_to_market_icon.png",
        title: this.calcService.getValue('tlOutput_C1_tlGTM')
      },
      {
        type: Constants.PAGE_TYPE_DECISION,
        route: Constants.SDPP_ROUTE,
        pageTitleRef: Constants.SDPP_PAGE_TITLE_REF,
        dropDownIndex:4,
        img: ICONS_PATH+"Service_delivery_icon.png",
        title: this.calcService.getValue("tlOutput_C1_tlSDPP")
      },
      {
        type: Constants.PAGE_TYPE_DECISION,
        route: Constants.INITIATIVES_ROUTE,
        pageTitleRef: Constants.INITIATIVES_PAGE_TITLE_REF,
        dropDownIndex:5,
        img: ICONS_PATH+"Strategic_Initiatives_icon.png",
        title: this.calcService.getValue("tlOutput_C1_tlSIs")
      },
    ]
    this.reportsNavigationModel=[
      {
        type: Constants.PAGE_TYPE_REPORT,
        route: Constants.DECISION_SUMMARY_ROUTE,
        pageTitleRef: Constants.DECISION_SUMMARY_PAGE_TITLE_REF,
        dropDownIndex:0,
        img: ICONS_PATH+"Decision_summary_icon.png",
        title: this.calcService.getValue("tlOutput_C1_tlDecSummary")
      },
      {
        type: Constants.PAGE_TYPE_REPORT,
        route: Constants.PNL_ROUTE,
        pageTitleRef: Constants.PNL_PAGE_TITLE_REF,
        dropDownIndex:1,
        img: ICONS_PATH+"Profit_and_loss_intro.png",
        title: this.calcService.getValue("tlOutput_C1_tlPnL")
      }
    ]
  }

  getDecisionNavigationItemByIndex(index:number){
    return this.decisionsNavigationModel[index];
  }

  getDecisionDropDownTitles(){
    return this.decisionsNavigationModel.map((navigationItem)=>{
      return navigationItem.title;
    })
  }

  getReportsDropDownTitles(){
    return this.reportsNavigationModel.map((navigationItem)=>{
      return navigationItem.title;
    })
  }

  getReportNavigationItemByIndex(index:number){
    return this.reportsNavigationModel[index];
  }

  getNavigationItemByRoute(route:string){
    let consolidatedModel=this.decisionsNavigationModel.concat(this.reportsNavigationModel)
    let item;
    consolidatedModel.forEach((element)=>{
      if(element.route==route){
        item=element;
      }
    })
    return item;
  }

  getReports(){
    return this.reportsNavigationModel;
  }

  getDecisions(){
    return this.decisionsNavigationModel;
  }

}
