import { Component, ViewChild, HostListener } from '@angular/core';
import { StorageService, CalcService, TextService, CommunicatorService, Constants } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';
import { BootstrapService } from '../../shared/bootstrap.service';
import { Subscription } from 'rxjs';
import { Constants as AppConstants } from '../../config/constants'

const KEY_NUM_1 = 49; // used to change the mode

@Component({
    selector: 'isma-holdscreen',
    templateUrl: './holdscreen.component.html',
    styleUrls: ['./holdscreen.component.styl'],
})

export class HoldScreenComponent {
    mode: number = 0;
    showPasswordErrorMessage: boolean = false;
    actualPassword: string = "";
    password: string = "";
    subscriber: Subscription;
    currentRound: number;

    @HostListener('document:keyup', ['$event'])
    onKeypress($event) {
        if ($event.keyCode == KEY_NUM_1 && $event.shiftKey && $event.ctrlKey) {
            this.mode = this.mode == 0 ? 1 : 0;
        }
    }

    constructor(
        private storageService: StorageService, private calcService: CalcService, private router: Router,
        private bootstrapService: BootstrapService, private textEngineService: TextService, 
        private communicator: CommunicatorService) { }

    ngOnInit() {
        let self = this;
        this.currentRound = +this.calcService.getValue('tlInputTeamRound');
        this.actualPassword = this.textEngineService.getText('RoundChangePassword') || 'bts287';
        this.mode = 0;
        this.subscriber = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe(() => {
            self.onModelChange();
        });
    }

    onModelChange() {
        let Round = +this.calcService.getValue('tlInputTeamRound');
        if (this.currentRound != Round) {
            this.currentRound = Round;
            this.nextRound();
        }
    }

    nextRound() {
        this.router.navigateByUrl('/registration');
    }

    ngOnDestroy() {
        this.subscriber.unsubscribe();
    }

    onPasswordSubmit() {
        if (this.password == this.actualPassword) {
            this.communicator.trigger(AppConstants.EVENTS.IMPORT_EXPORT_TRIGGER, 'Import')
        }else {
            this.showPasswordErrorMessage = true;
        }
    }

    exportData(){
        this.communicator.trigger(AppConstants.EVENTS.IMPORT_EXPORT_TRIGGER, 'Inputs')
    }
}
