import { Component, OnInit, Input,EventEmitter, Output, ViewChild, TemplateRef } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { TextEngineService } from '../textengine/textengine.service';
import { CommonModalComponent } from '../common-modal/common-modal.component';


@Component({
    selector: 'ism-confirmation-popup',
    templateUrl: './confirmation-popup.html',
    styleUrls: ['./confirmation-popup.styl']
})
export class ConfirmationPopup implements OnInit {
    @Input() headerIcon = 'fa-compass'
    @Input() headerTextKey: string = "Confirm";
    @Input() messageTextKey: string = "confirmSubmitMessage";
    @Output() confirmClicked: EventEmitter<any> = new EventEmitter();
    @ViewChild('popupContentRef', {static: false}) public popupContentRef: CommonModalComponent;

    constructor(protected calcService: CalcService,
        protected textEngine: TextEngineService) { }

    ngOnInit() { }

    showAlert(){
        this.popupContentRef.openModal();
    }

    onCloseClicked() {
        this.popupContentRef.closeModal();
    }

    onConfirmClicked() {
        this.onCloseClicked();
        this.confirmClicked.emit();
    }

}
