import { Component, OnInit, Input, ViewChild, TemplateRef } from '@angular/core';
import { TextEngineService } from '../../shared/textengine/textengine.service';
import { ModalDirective, BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-reviewwobbler',
  templateUrl: './reviewwobbler.component.html',
  styleUrls: ['./reviewwobbler.component.styl']
})
export class ReviewwobblerComponent implements OnInit {
  @Input() reviewObject: any;
  @Input() reviewSceneId: any;
  @Input() selectedAnswer = -1; // A - 0, B - 1, ....

  choices = [];
  reviewAnswer = -1; // A - 0, B - 1, ....
  reviewVisible: boolean = false;
  reviewModalRef: BsModalRef;
  @ViewChild('reviewModal', { static: true }) public reviewModal: TemplateRef<any>;
  config = {
    class: 'modal-lg review-modal animated fadeInUp',
    ignoreBackdropClick: true,
    focus: true,
    keyboard: false
  }



  constructor(
    private textEngineService: TextEngineService,
    public modalService: BsModalService
  ) { }

  ngOnInit() {
    if(this.reviewObject == undefined){
      this.reviewObject = this.textEngineService.getScene(this.reviewSceneId)
    }
    this.choices = this.textEngineService.getSingleSelectChoices(this.reviewSceneId);
  }

  showReviewModal() {
    this.reviewVisible = true;
    this.reviewAnswer = this.selectedAnswer
    // this.reviewModal.show();
    this.reviewModalRef = this.modalService.show(this.reviewModal,this.config)
  }

  hideReviewModal() {
    // this.reviewModal.hide();
    this.reviewModalRef.hide();
    this.reviewVisible = false;
  }

}
