import { Component, OnInit } from '@angular/core';
import { CalcService, TextService, StorageService } from '@btsdigital/ngx-isomer-core';
import { Router } from "@angular/router";
import { Constants } from '../../config/constants';
import { BootstrapService } from '../../shared/bootstrap.service';
import { Subscription } from 'rxjs';

const INITIAL_VALUE = 'HelloCloud'

@Component({
  selector: 'ism-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.styl']
})
export class RegistrationComponent implements OnInit {
  
  teamName='';
  showErrorMsg: boolean=false;
  currentYear: number;

  constructor(private calcService: CalcService, private textService: TextService,private router: Router,private storageService:StorageService, 
              private bootstrapService: BootstrapService) { }

  ngOnInit() {
    this.currentYear = +this.calcService.getValue('tlInputTeamRound');
    
    this.teamName = this.calcService.getValue('tlInputTeamName');
    this.storageService.getValue(Constants.EVENTS.INTRO_COMPLETE).then((val) => {
      if ((val == undefined || val != 'true') && this.teamName == INITIAL_VALUE) {
        this.teamName = '';
      }
    })
  }

  onSubmit(){
    if(this.teamName.length!=0){
      this.showErrorMsg= false;
      this.navigateToMainPage()
    }
    else{
      this.showErrorMsg=true;
    }
  }


  navigateToMainPage(){
    // Set Team Name
    if (this.currentYear == 1) {
      if (this.teamName.match(/^\d/)) {
        //In case Team name starts with a number
        this.teamName = '`' + this.teamName;
      }
      this.calcService.setValue('tlInputTeamName', this.teamName);
    }

    // Set status for correct routing on refresh
    let round = +this.calcService.getValue('tlInputTeamRound');
    let key = round == 1 ? Constants.EVENTS.ROUND_ONE_STARTED :
    round == 2 ? Constants.EVENTS.ROUND_TWO_STARTED :
    round == 3 ? Constants.EVENTS.ROUND_THREE_STARTED : Constants.EVENTS.INTRO_COMPLETE;
    
    this.storageService.setValue(key, 'true');
    this.storageService.setValue(Constants.EVENTS.INTRO_COMPLETE,"true");

    // Route
    this.router.navigateByUrl("/intro")
  }

}
