import { Component, OnInit, TemplateRef, Input, ViewChild, ViewContainerRef, ElementRef } from '@angular/core';
import { ModalOptions, ModalDirective } from 'ngx-bootstrap/modal';

@Component({
  selector: 'ism-common-modal',
  templateUrl: './common-modal.component.html',
  styleUrls: ['./common-modal.component.styl']
})
export class CommonModalComponent implements OnInit {
  @Input() className = 'modal-sm'
  @Input() contentClassName = '';
  @ViewChild('childModal', { static: true }) public childModal: ModalDirective;
  modal: ElementRef;
  isOpen: boolean = false;

  constructor(  ) { }

  ngOnInit() {
  }

  openModal() {
    var body = document.getElementsByTagName("body")[0];
    this.modal = this.childModal["_element"];
    if (this.modal) {
      if(body.prepend){
        body.prepend(this.modal.nativeElement);
      }else{
        // IE does not have prepand
        body.appendChild(this.modal.nativeElement)
      }
    }
    this.isOpen = true;

    this.childModal.show();
  }

  closeModal() {
    var body = document.getElementsByTagName("body")[0];
    if (this.modal) {
      body.removeChild(this.modal.nativeElement);
    }
    this.isOpen = false;
    this.childModal.hide()
  }

}
